/* navbar main container */
.navbar-container{
    width:20%;
    height:100vh;
}
/* logo container */
.logo{
    width:100%;
    height:30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


}
/* logo container bottom bar */
.logo::after{
    content:"";
    width:95%;
    height:2px;
    background-color:rgb(231, 231, 231);
  
    margin-top:20px;
}
/* logo image */
.logo img{
    width:25%;
    object-fit: contain;
    margin-bottom:20px;
}

/* main navbar */
.navbar-box{

    width:100%;
    display: flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    position: relative;
}
/* navbar bottom line for design */
.navbar-box::after{
    content:"";
    width:95%;
    height:2px;
    background-color:rgb(231, 231, 231);
    margin-top:30px;
}

.navbar-box li{
    list-style-type:none;
    margin:10px 0px;
    width:65px;
    
    text-align:left !important;



}

.navbar-box li a{
    text-decoration: none;
    color:rgb(42, 51, 85);
    font-size:0.9rem;
    font-family:'Gill Sans', 'Gill Sans MT', 'Trebuchet MS', sans-serif;
    font-weight:bold;
    width:200px;
   
}

/* navbar text hover effect */
.navbar-box li a::before{
    content:"";
    width:2%;
    height:19px;
    background-color: rgb(255, 128, 0);
    position:absolute;
    left:0px;
    margin-top:0px;
    transition:all 1s ease;
      
}

.navbar-box li a:hover::before{
    width:70%;
    -webkit-filter: opacity(0.3);
            filter: opacity(0.3);

}

/* button content */
.button-content{
    width: 100%;
    height:20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
}
.button-content div{
    width:130px;
    padding:10px 0px;
    text-align: center;
    margin:10px 0px;
    color:white;
    font-family:Arial, Helvetica, sans-serif;
    cursor: pointer;
  
}
.discount{
    background-color:rgb(255, 99, 32);
    position: relative;
    overflow: hidden;
}
.discount::after{
    content:"";
    width:100%;
    height:0px;
    background-color: rgb(255, 255, 255);
    -webkit-filter:opacity(0.6);
            filter:opacity(0.6);
    position: absolute;
    border-radius:0px 100% 0% 0%;
    bottom:0px;
    left:0px;
    transition:all .7s ease-in-out;
}
.discount:hover::after{
    height:120px;
    z-index:1;

}

.discount p{
    z-index:1000;
}
.new-this-week{
    background-color:rgb(54, 54, 83);
    position: relative;
    overflow: hidden;
}
.new-this-week::after{
    content:"";
    width:100%;
    height:0px;
    background-color: rgb(255, 255, 255);
    -webkit-filter:opacity(0.6);
            filter:opacity(0.6);
    position: absolute;
    border-radius:0px 00px 100% 0%;
    top:0px;
    left:0px;
    transition:all .7s ease-in-out;
}
.new-this-week:hover:after{
    height:150px;
}
/* end of this button content */

.cart-search-favourite-container{
