}



/* ================================================ */

.alertproductadd{
    position:fixed;
    top:0px;
    top:0px;
    left:50%;
    background-color:rgb(255, 147, 79);
    padding:10px 20px;
    font-size: 1rem;
    z-index:9999;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    transition:all 1s ease;
    opacity:0.1;
