}

.alertproductaddTransition{
    position:fixed;
    top:0px;
    top:0px;
    left:50%;
    background-color:rgb(255, 147, 79);

    padding:10px 20px;
    font-size: 1rem;
    z-index:9999;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    transition:all 0.5s ease;
    opacity:1;
    color:white;
}




