/* categories list  */
.categories-list > li{   

    list-style-type:none;
    cursor: pointer;
    position: relative;
    margin-left:15px;
  
}
.categories-list > li > div
{
    background-color:rgb(255, 255, 255);
    /* border:1px solid rgb(70, 69, 69); */
    display: flex;
    align-items: center;
    flex-direction:column;
    border-radius:1px;
    /* box-shadow:1px 2px 2px rgb(101, 101, 101); */
   
}

.categories-list > li > div > h1{
    font-size:1rem;
    color:rgb(56, 56, 56);
 
    padding:6px 9px;
