    -webkit-filter: opacity(1);
            filter: opacity(1);
}
.brands > li > ul >li{

    display: flex;
    justify-content: center;
    color:rgb(73, 73, 73);
    opacity:0;
}
.brands > li:hover > ul > li{
    opacity: 1;
    transition:all 2s ease;
}
.brands li > ul > li > *{
